import React, { useState } from 'react'
import banner from '../images/clientPageBanner.jpg'

import "../styles/Client.css"

export default function Client() {

    const [orderHistory, setOrderHistory] = useState([
        { orderId: "ord001", billStatus: true, typeOfOrder: "Android App", status: "Delivered" },
        { orderId: "ord002", billStatus: true, typeOfOrder: "Android App", status: "Delivered" },
        { orderId: "ord003", billStatus: false, typeOfOrder: "Android App", status: "Delivered" },
        { orderId: "ord004", billStatus: true, typeOfOrder: "Android App", status: "Delivered" },
        { orderId: "ord005", billStatus: true, typeOfOrder: "Android App", status: "Delivered" },
    ])

    const [ordersQueue, setordersQueue] = useState([
        { orderId: "ord001", billStatus: true, typeOfOrder: "Android App" },
        { orderId: "ord002", billStatus: true, typeOfOrder: "Android App" },
    ])

    return (
        <div>
            <div className="clientPageBanner">
                <img src={banner} alt="client-page-banner" />
            </div>

            <div className="ClientButtons">
                <button>Place New Order</button>
                <button>Contact Team Leader</button>
                <button>Request For Revision</button>
            </div>

            <div className="ClientDashboard">
                <div className="CurrentOrderClient">
                    <h3>Current Orders</h3>
                    <ul>
                        {
                            ordersQueue.map(orders => (<OrderQueue key={orders.orderId} orderId={orders.orderId} typeOfOrder={orders.typeOfOrder} />))
                        }
                    </ul>
                </div>

                <div className="OrderHistoryClient">
                    <h3>Previous Orders</h3>
                    <ul>
                        {
                            orderHistory.map(order => <OrderLister key={order.orderId} orderId={order.orderId} typeOfOrder={order.typeOfOrder} status={order.status} />)
                        }
                    </ul>
                </div>
            </div>



        </div>
    )
}

function OrderLister(props) {
    return (
        <li>
            <p>Order ID: {props.orderId}</p>
            <p>Type Of Order: {props.typeOfOrder}</p>
            <p>Status: {props.status}</p>
            <button>View Details</button>
        </li>
    )
}

function OrderQueue(props) {
    return (
        <li>
            <p>Order ID: {props.orderId}</p>
            <p>Type Of Order: {props.typeOfOrder}</p>
            <button>View Details</button>
        </li>
    )
}