import React, { useState } from 'react'

export default function DebdashBoard() {

    const [currentOrder, setcurrentOrder] = useState({
        orderId:"ord001",
        billStatus:true,
        typeOfOrder:"Android App for School",
        description:`Lorem ipsum dolor sit amet, consectetur adipiscing elit, 
        sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. 
        Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi 
        ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in
        voluptate velit esse cillum dolore eu fugiat nulla pariatur.Excepteur
        sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt 
        mollit anim id est laborum.`,
        deliveryDate:"12/6/2021"
    });

    const [TeamLeadMessages, setTeamLeadMessages] = useState([
        {E_ID:"e001",Name:"Leader",Message:"goo goo gaa gaa"},
        {E_ID:"e002",Name:"TSM",Message:"poo poo pee pee"}
    ])

    return (
        <div>
            <div>
                <h2>Current Order</h2>
                <h4>Order Title: {currentOrder.typeOfOrder}</h4>
                <h5>Order ID: {currentOrder.orderId} <br/> Order Bill Status: {currentOrder.billStatus?"Paid":"Due"}</h5>
                <p><span>Description: </span>{currentOrder.description}</p>
                <h4>Delivery Date: {currentOrder.deliveryDate}</h4>
            </div>

            <div>
            <button>Submit Task</button>
            <button>Rejection Request</button>
            <br/><br/>
            <label>Message To Leader: </label>
            <input type="text"/>
            <button>Send</button>
            </div>

            <div>
             <h2>Messages from TeamLead DIV</h2>
                    <ul>
                    {
                       TeamLeadMessages.map(message =>(<LeaderMessages key={message.E_ID} Name={message.Name} Message={message.Message}/>))
                    }
                    </ul>
            </div>
        </div>
    )
}

function LeaderMessages(message){
    return(
        <li>
         <h5>Name: {message.Name}</h5>
         <p>Message: {message.Message}</p>
         <button>Remove</button>
        </li>
    )
}